import React, {Component} from "react"
import {connect} from "react-redux"
import {
    saveSelectedVoipNumber,
    selectVoipNumber,
    selectVoipVariant,
    showVoipVariantModal,
    voipSelection
} from "../actions/voip"
import OraModal from "../../core/components/ui/Modal"
import OraRadioButton from "./OraRadioButtonGroup";
import CmsDescription from "../../core/components/ui/CmsDescription";
import * as FormatUtils from "../../../utils/FormatUtils";
import {getDesignationNumbers, getShowVoipVariantModal} from "../selectors/voip";
import VoipVariant from "../enum/VoipVariant";

class VoipVariantComponent extends Component {

    constructor(props){
        super(props);
        this.onAccept=this.onAccept.bind(this);
        this.state = {
            voipVariant: undefined,
            hideModal: false
        }
    }

    onAccept(e) {
       e.preventDefault();
       this.props.selectVoipVariant(this.state.voipVariant);
       if (this.state.voipVariant === VoipVariant.NP_INT) {
           this.props.selectVoipNumber(this.props.designationNumbers[0]);
           this.props.saveSelectedVoipNumber(this.props.designationNumbers[0]);
       }
       this.onClose();
    }

    onClose() {
        if (this.state.voipVariant !== VoipVariant.NEW_VOIP) {
            this.props.stopVoipSelection();
        }
        this.setState({ hideModal: true});
        setTimeout(() => {this.props.closeModal();this.setState({voipVariant: undefined, hideModal: false})}, 400);

    }

    onValueChange({value}) {
        this.setState({voipVariant: value});
    }

    formatNumber(number) {
        return FormatUtils.formatNumber(number, '## ### ## ##');
    }

    getConnectNumber(selectedNumber) {
        return this.props.descriptions.connectNumber.replace("XXXXXXXXX", this.formatNumber(selectedNumber));
    }

    render() {
        return (
            <OraModal onClose={() => this.onClose()} open={this.props.showModal && !this.state.hideModal} id={"voipVariantsModal"} showClose={true}
                      escapeClose={true} clickClose={true} size="narrow">
                <div className="row">
                    <div className="l-col l-col-small-12 l-col-medium-12 l-col-12 ">
                        <CmsDescription description={this.props.descriptions['firstTitle']}/>
                    </div>
                </div>
                <div className="row">
                    <div className="l-col l-col-small-12 l-col-medium-12 l-col-12 ">
                        <fieldset className="opl-radiogroup opl-inputgroup opl-inputgroup--vertical u-padding-m u-padding-top-m u-padding-left-m">
                            <OraRadioButton name="option" text={this.props.descriptions['newNumber']}
                                            value={VoipVariant.NEW_VOIP} onValueChange={this.onValueChange.bind(this)}
                                            selectedValue={this.state.voipVariant}/>
                            <OraRadioButton name="option"
                                            text={this.getConnectNumber(this.props.designationNumbers[0])}
                                            value={VoipVariant.NP_INT} onValueChange={this.onValueChange.bind(this)}
                                            selectedValue={this.state.voipVariant}/>
                        </fieldset>
                    </div>
                </div>
                <div className="row">
                    <div className="l-col l-col-small-12 l-col-medium-12 l-col-12 ">
                        <CmsDescription description={this.props.descriptions['descriptionText1']}/>
                    </div>
                </div>
                {
                    this.state.voipVariant === VoipVariant.NP_INT &&
                    <div className="row">
                        <div className="l-col l-col-small-12 l-col-medium-12 l-col-12 u-padding-top-m">
                            <CmsDescription description={this.props.descriptions['descriptionText2']}/>
                        </div>
                    </div>
                }
                <div className="row">
                    <div className="l-col l-col-small-12 l-col-medium-12 l-col-12 u-padding-top-l u-padding-l">
                        <button className="o-btn opl-btn opl-btn--primary u-w-100" onClick={this.onAccept}
                                disabled={!this.state.voipVariant}>Dalej
                        </button>
                    </div>
                </div>
            </OraModal>

        );
    }
}


const mapStateToProps = state => ({
    showModal: getShowVoipVariantModal(state),
    designationNumbers: getDesignationNumbers(state),
});

const mapDispatchToProps = dispatch => ({
    closeModal: () => dispatch(showVoipVariantModal(false)),
    selectVoipVariant: (voipVariant) => dispatch(selectVoipVariant(voipVariant)),
    stopVoipSelection: () => dispatch(voipSelection(false)),
    selectVoipNumber: (number) => dispatch(selectVoipNumber(number)),
    saveSelectedVoipNumber: (number) => dispatch(saveSelectedVoipNumber(number)),
});

export default connect(mapStateToProps, mapDispatchToProps)(VoipVariantComponent);